.main-app {
    text-align: center;
    background-color: rgb(0,0,0,1);
    color:  white;
    height: 100%
}

.font-link {
    font-family: 'Hanalei Fill', cursive;
  }
  
.cta-button {
    padding: 15px;
    border: none;
    border-radius: 12px;
    min-width: 250px;
    color: white;
    font-size: 18px;
    cursor: pointer;
}
.center {
    margin-left: auto;
    margin-right: auto;
    font-family: "VT323";
}

.text-center {
    text-align: center;
    font-size: 15px;
    font-family: "VT323";
}
.text-center-small {
    text-align: center;
    font-size: 10px;
    font-family: "VT323";
}

.text-center-big {
    text-align: center;
