    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;
    box-shadow: 0 0 20px #3168d8;
    border-radius: 10px;
    position:relative;
    right:-5px; 
    top:3px;
    transform: translateY(4px);
    
}

.fetch-opensea-button{
    background-image: linear-gradient(to right, #fbc2eb 0%, #a6c1ee 51%, #fbc2eb 100%);
    flex: 1 1 auto;
    margin: 2px;
    padding: 2px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;
    position:relative;
    right:-5px; 
    top:3px;
    box-shadow: 0 12px 16px 0 #3168d8, 0 17px 50px 0 #3168d8;
}

.connect-wallet-button {
    background-image: linear-gradient(to right, #3168d8 0%, #3168d8 51%, #3168d8 100%);
